import { Component, h } from 'preact'
import convert from 'xml-js'
import Helmet from 'react-helmet'
import { get, filter, find } from 'lodash'
import Button from '../../components/button'
import MusicEntry from '../../components/music-entry'
import styles from './style.css'

const BUCKET = 'https://pn-mytracks.s3.eu-central-1.amazonaws.com/'

/**
 * Loads the amazon directory listing as XML
 * converts it to json, and returns new state objects
 *
 * @param {string} url load from this ressource
 * @param {obj} that this
 */
const LoadList = (url, that) => {
  // get the file listing from amazon
  window.fetch(url).then((data) => {
    data.text().then((body) => {
      // that.setState({})
      const json = convert.xml2js(body)
      const list = get(convert.xml2js(body), 'elements[0].elements')
      const Continue = get(find(get(json, 'elements[0].elements'), { name: 'NextContinuationToken' }), 'elements[0].text')
      that.setState({
        continuationToken: Continue,
        response: json,
        list: filter(list, { name: 'Contents' })
      }
      )
    })
  })
}

class Home extends Component {
  constructor () {
    super()
    this.state = {
      continuationToken: false,
      response: {},
      list: [],
      playingSource: false
    }
  }

  componentDidMount () {
    LoadList(BUCKET + '?list-type=2', this)
  }

  handleNextPage () {
    let URL = BUCKET + '?list-type=2'
    if (this.state.continuationToken) {
      URL = URL + '&continuation-token=' + encodeURIComponent(this.state.continuationToken)
    }
    console.log('load next:  ', URL)
    LoadList(URL, this)
  }

  render () {
    const that = this
    return (
      <div class={styles.container}>
        <Helmet>
          <title>ProducerNetwork Player</title>
          <meta
            name='description'
            content='This is the player for all the old tunes people submitted around 2000-2010. '
          />
          <meta property='og:title' content='ProducerNetwork Player' />
          <meta
            property='og:description'
            content='This is the player for all the old tunes people submitted around 2000-2010. '
          />
        </Helmet>
        <img
          alt='image'
          src='https://aheioqhobo.cloudimg.io/v7/_playground-bucket-v2.teleporthq.io_/7f3d6922-ee3d-4640-a752-d5444f28fe87/5302d73e-4848-419d-82a7-220c88b152c5?org_if_sml=1&amp;w=1200'
          class={styles.image}
        />
        <div class={styles.container1}>
          <h1 class={styles.text}>PN WEB PLAYER</h1>
          <span class={styles.text1}>
          Hier könnt Ihr Songs von alten <a href='https://producer-network.de'>Producer-Network</a> Produzenten anhören. Die Tracks sind alle
          zwischen 2000-2010 entstanden.
          </span>
        </div>
        <div class={styles.container2}>
          <div onClick={e => this.handleNextPage()}>
            <Button textButton='next page' />
          </div>
          {this.state.playingSource &&
            <audio controls autoPlay key={this.state.playingSource}>
              <source src={BUCKET + encodeURIComponent(this.state.playingSource)} type='audio/mp3' />
            </audio>}
        </div>
        <div class={styles.container3}>
          {this.state.list.map((el) => {
            const key = get(el, 'elements[0].elements[0].text', 'empty')
            return <MusicEntry key={key} textFilename={key} download={(textFilename) => { window.location = BUCKET + textFilename }} updateSource={(url) => { that.setState({ playingSource: url }) }} />
          })}
        </div>
      </div>
    )
  }
}

export default Home
