import { Component, h } from 'preact'

import PropTypes from 'prop-types'

import Button from './button'
import styles from './music-entry.css'

class MusicEntry extends Component {
  render (props) {
    return (
      <div class={styles.container}>
        <div class={styles.container1} onClick={() => props.updateSource(props.textFilename)}>
          <Button />
        </div>
        <div class={styles.container2} onClick={() => props.download(props.textFilename)}>
          <Button textButton='mp3' />
        </div>
        <div class={styles.container3}>
          <span class={styles.text}>{props.textDir}</span>
          <span class={styles.text1}>{props.textSlash}</span>
          <span class={styles.text2}>{props.textFilename}</span>
        </div>
      </div>
    )
  }
}

MusicEntry.defaultProps = {
  textButtonPlay: 'breakbeats',
  textSlash: '/',
  textDir: 'breakbeats',
  textFilename: 'polarity-meine-musik_kannauchl\xE4ngersein.mp3'
}

MusicEntry.propTypes = {
  updateSource: PropTypes.func,
  textButtonPlay: PropTypes.string,
  textSlash: PropTypes.string,
  textDir: PropTypes.string,
  textFilename: PropTypes.string
}

export default MusicEntry
